// Mob Nav
.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(64, 106, 159, 0.98);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  span{
    display: none;
    color: #FFF;
    font-size: 25px;
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
  }

  &.open {
    opacity: 1;
    visibility: visible;

    span{
      display: block;
    }
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-white;
  z-index: 9999;
  overflow-y: auto;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #4b4c4c;
      width: 32px;
      height: 32px;
      background-color: transparent;
    }

    .search_pop {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $color-sec;
      color: #fff;
      font-size: 14px;
      margin-left: auto;
      margin-right: 20px;

      html[dir="ltr"] & {
        margin-left: 20px;
        margin-right: auto;
      }
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: #777;
      cursor: pointer;
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 10px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      opacity: 0;
      width: 115px;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }
      &:nth-child(5) {
        transition-delay: 1.1s;
      }
      &:nth-child(6) {
        transition-delay: 1.3s;
      }

      a {
        width: 100%;
        display: block;
        font-size: 17px;
        color: #4b4c4c;
        text-align: center;
        transition: 0.5s;
      }

      .dropDown{
        display: none;
        margin-top: 10px;

        li{
          &:not(:last-child){
            margin-bottom: 6px;
          }

          a{
            font-size: 15px;
            color: #777
          }
        }
      }

      i {
        position: absolute;
        top: 17px;
        left: 17px;
        // transform: rotate(90deg);
        font-size: 9px;
        color: #4b4c4c;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(180deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

