// Start Header

// Navbar
.navBar {
  padding: 15px 0;
  background-color: #fff;
  transition: 0.5s;
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: #fff;

  &.scroll {
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  }

  .content {
    position: relative;
    @include Flex;
    justify-content: space-between;

    .logo{
      img{
        @include Sm{
          width: 110px !important;
        }
      }
    }

    .searchTriger{
      font-size: 25px;
      margin: 0 auto 0 20px;
      html[dir="ltr"] & {
        margin: 0 15px 0 auto;
      }
      color:#406a9f;
      display: none;

      @include Sm{
        display: block;
      }
    }

    .menuTriger{
      font-size: 30px;
      color:#406a9f;
      display: none;

      @include Sm{
        display: block;
      }
    }

    .links {
      margin: 0 auto 0 83px;

      @include Md{
        margin: 0 auto 0 33px;
        
        html[dir="ltr"] & {
          margin: 0 33px 0 auto;
        }
      }

      @include Lg{
        margin: 0 auto 0 83px;
        
        html[dir="ltr"] & {
          margin: 0 83px 0 auto;
        }
      }

      @include Sm{
        display: none;
      }

      ul {
        display: flex;

        li {
          &:not(:last-child) {
            margin-left: 25px;

            html[dir="ltr"] & {
              margin-left: 0;
              margin-right: 25px;
            }
          }

          a {
            font-family: "tjMd";
            font-size: 17px;
            color: #4b4c4c;
            transition: 0.5s;

            @include Md{
              font-size: 16px;
            }

            @include Lg{
              font-size: 17px;
            }

          }

          &.drop {
            position: relative;

            .dropDown {
              position: absolute;
              right: calc(-1 * (160px / 2) + 30px);
              top: 140%;
              width: 160px;
              background-color: rgba($color-prim, 0.95);
              padding: 7px 10px;
              opacity: 0;
              visibility: hidden;
              z-index: 9;
              transform: translateY(-10px);
              border-radius: 4px;
              transition: 0.5s;

              html[dir="ltr"] & {
                left: calc(-1 * (160px / 2) + 40px);
                right: unset;
                width: 190px;
                // padding: 15px 10px;
              }

              &:after {
                content: "";
                width: 0;
                height: 0;
                display: block;
                border-width: 6px;
                border-color: transparent transparent $color-prim;
                border-style: solid;
                bottom: 100%;
                position: absolute;
                right: 50%;
                transform: translateX(50%);
              }

              ul {
                display: flex;
                flex-direction: column;
                li {
                  margin: 0;

                  &:not(:last-child) {
                    border-bottom: 1px solid #577AA9;
                  }

                  a {
                    color: rgb(216, 233, 255);
                    line-height: 2.4;
                    font-size: 15px;
                    font-family: "tj";
                    text-align: center;
                    display: block;
                    width: 100%;

                    html[dir="ltr"] & {
                      font-size: 14px;
                    }

                    &:after {
                      display: none;
                    }

                    &:hover {
                      color: #fff;
                    }
                  }
                }
              }
            }

            &:hover {
              .dropDown {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
              }
            }

            > a {
              position: relative;
              z-index: 99;
              &:after {
                content: "\f078";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                display: inline-block;
                color: #4b4c4c;
                font-size: 9px;
                margin-right: 5px;
                transition: 0.5s;

                html[dir="ltr"] & {
                  margin-right: 0;
                  margin-left: 5px;
                }
              }

              &:hover {
                &:after {
                  color: #406aa0;
                }
              }
            }
          }

          &:hover {
            > a {
              color: #406aa0;
              &:after {
                color: #406aa0;
              }
            }
          }
        }
      }
    }

    .search {
      position: relative;
      width: 263px;

      @include Sm{
        position: absolute;
        top: 230%;
        width: 60vw;
        left: 50%;
        transform: translateX(-50%) translateY(-20px);
        z-index: 999;
        opacity: 0;
        visibility: hidden;
        transition: .5s;

        &.open{
          opacity: 1;
          visibility: visible;
          transform: translateX(-50%) translateY(0);
        }

        input{
          &:focus{
            border-color: transparent !important;
          }
        }
      }

      @include Xs{
        width: 90vw;
      }

      input {
        width: 100%;
        height: 45px;
        border-radius: 100px;
        border: 2px solid #eceaea;
        padding: 0 28px;
        font-size: 14px;
        color: #b5b5b5;
        transition: 0.5s;

        &::placeholder {
          color: #b5b5b5;
        }

        &:focus {
          border: 2px solid $color-prim;

          + button span {
            color: $color-prim;
          }
        }
      }

      button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 20px;
        html[dir="ltr"] & {
          left: unset;
          right: 20px;
        }
        background-color: transparent;
        border: 0;
        display: flex;
        align-items: center;

        span {
          font-size: 18px;
          color: #4b4c4c;
        }
      }
    }

    .lang {
      position: absolute;
      top: -10px;
      left: 15px;
      html[dir="ltr"] & {
        left: auto;
        right: 15px;
      }
      font-size: 13px;
      color: #aaaaaa;
      transition: 0.5s;

      @include Sm{
        display: none;
      }

      &:hover {
        color: $color-prim;
      }
    }
  }
}
// End Of Header

// Main Slider

.main-slider {
  margin-top: 0px;
  // position: relative;
  // z-index: -1;

  .owl-nav {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    padding: 0 60px;
    height: 0;
    display: flex;
    display: none !important;
    justify-content: space-between;
    align-items: center;

    @include Sm {
      display: none;
    }

    button {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba(#3c3c3c, 0.5) !important;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;

      &:hover {
        background-color: $color-prim !important;
      }

      i {
        font-size: 10px;
        color: #fff;
      }
    }
  }

  .owl-dots {
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include mob600 {
      bottom: 10px;
    }

    button {
      &:not(:last-child) {
        margin-left: 5px;
      }

      &.active {
        span {
          background-color: $color-white;
        }
      }

      span {
        display: block;
        width: 8px;
        height: 8px;
        background-color: rgba(#000, 0.5);
        border-radius: 50%;
      }
    }
  }
}

