// Services

.services {
  padding: 70px 0;
  text-align: center;

  @include Md{
    padding: 45px 0 60px;
  }

  @include Lg{
    padding: 70px 0;
  }

  @include Sm {
    padding: 30px 0 0px;
  }

  .heading {
    h2 {
      font-size: 28px;
      color: rgb(75, 76, 76);
      font-family: "tjBold";
      margin-bottom: 50px;
    }
  }
  .services-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media (max-width: 991px) {
      margin-bottom: 50px;
    }

    .img-box {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.15);
      width: 220px;
      height: 220px;
    }
    .content {
      margin-top: 35px;
      h3 {
        font-family: "tjMd";
        font-size: 22px;
        color: rgb(75, 76, 76);
        margin-bottom: 18px;
        margin-top: 0;
      }
      p {
        font-size: 17px;
        color: rgb(75, 76, 76);
        line-height: 1.471;
        font-family: "tj";
        margin: auto;
        margin-bottom: 27px;
        width: 90%;
        @media (max-width: 1024px) {
          width: 95%;
        }

        @media (max-width: 991px) {
          width: 80%;
        }
        @media (max-width: 425px) {
          width: 100%;
        }
      }
      a {
        background-color: rgb(242, 242, 242);
        text-decoration: none;
        width: 149px;
        height: 40px;
        color: rgb(135, 135, 135);
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        border-radius: 100px;
        font-size: 14px;
        font-family: "tjMd";
        transition: 0.5s;

        span {
          margin-right: 30px;
          html[dir="ltr"] & {
            margin-left: 30px;
            margin-right: 0px;
          }
          font-size: 18px;
        }
        &:hover {
          background-color: rgb(92, 174, 69);
          color: #fff;
        }
      }
    }

    &.firstB{
      img{
        filter: invert(57%) sepia(24%) saturate(972%) hue-rotate(63deg) brightness(98%) contrast(93%);
      }
    }

    &.center {
      .img-box {
        img {
          filter: invert(56%) sepia(87%) saturate(947%) hue-rotate(346deg)
            brightness(95%) contrast(91%);
        }
      }
      .content a {
        &:hover {
          background-color: #e88522;
        }
      }
    }
    &.left {
      .img-box {
        img {
          filter: invert(64%) sepia(28%) saturate(18%) hue-rotate(14deg)
            brightness(89%) contrast(101%);
        }
      }
      .content a {
        &:hover {
          background-color: #999999;
        }
      }
    }
  }
}

