// Customers

.customers {
  padding: 70px 0 80px;
  background-color: #f7f7f7;
  text-align: center;

  @include Md{
    padding: 40px 0 60px;
  }

  @include Lg{
    padding: 70px 0 80px;
  }

  @include Sm {
    padding: 40px 0 40px;
  }

  @media (max-width: 425px) {
    padding: 50px 0;
  }

  .heading {
    h2 {
      font-size: 28px;
      color: rgb(75, 76, 76);
      font-family: "tjBold";
      margin-bottom: 30px;
      @media (max-width: 768px) {
        margin-bottom: 20px;
      }
    }
    p {
      font-size: 22px;
      color: rgb(75, 76, 76);
      font-family: "tj";
      margin-bottom: 0;
      @media (max-width: 768px) {
        font-size: 18px;
      }
    }
  }
  .slider-container {
    margin-top: 40px;
    position: relative;
    @media (max-width: 991px) {
      margin-top: 30px;
    }

    .owl-stage {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .item {
      padding: 20px;
      @media (max-width: 991px) {
        padding: 17px;
      }

      img {
        filter: grayscale(1);
        opacity: 0.6;
        transition: 0.5s;
      }
      &:hover {
        img {
          filter: grayscale(0);
          opacity: 1;
        }
      }
    }
    .owl-nav {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      margin-top: 0px;
      z-index: 99;

      @include Md{
        width: 95%;
      }

      @include Lg{
        width: 100%;
      }

      @media (max-width: 991px) {
        display: none;
      }

      .owl-prev,
      .owl-next {
        position: absolute;
        top: 50%;
        transform: translateY(-130%);
        outline: none;
        background-color: unset;
        font-size: 24px;
        color: rgb(178, 178, 178);
        transition: 0.5s;
        &:hover {
          color: rgb(64, 106, 160);
        }
      }
      .owl-prev {
        right: -70px;
        @media (max-width: 1024px) {
          right: -30px;
        }
      }
      .owl-next {
        left: -70px;
        @media (max-width: 1024px) {
          left: -30px;
        }
      }
    }
    .owl-dot {
      outline: none;
      margin-top: 42px;
      @media (max-width: 991px) {
        margin-top: 22px;
      }

      &:not(:last-child){
        margin-left: 9px;
      }

      span {
        display: block;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.4);
        width: 8px;
        height: 8px;
        transition: 0.5s;
      }
      &.active {
        span {
          background-color: rgb(64, 106, 160);
        }
      }
    }
  }
}

