      font-size: 20px;
      font-family: "tjMd";
      color: #4b4c4c;
      line-height: 1.2;
      margin-top: 30px;
      transition: 0.5s;
    }

    p {
      font-size: 16px;
      font-family: "tj";
      color: #7c7c7c;
      line-height: 1.8;
      margin-bottom: 0;
    }

    &:hover {
      img {
        animation: flash 0.5s linear;
      }

      h3 {
        color: rgb(64, 106, 160);
      }
    }
  }
}

// News

// footer

footer {
  padding: 80px 0 0;
  padding-bottom: 0;
  background-color: #40699e;

  @include Sm {
    padding: 40px 0 0;
    margin-top: 0;
  }

  .col-md-3{
    padding: 0;
  }

  .map{
    position: relative;
    @include Sm {
      display: none;
    }

    .pin{
      position: absolute;

      &.pin-1{
        top: 42%;
        left: 44%;
      }

      &.pin-2{
        top: 35%;
        left: 35%;
      }

      &.pin-3{
        top: 52%;
        left: 35%;
      }
    }
  }

  .col-md-4 {
    @include Md{
      padding-right: 0;
      transform: translateX(15px);
      html[dir="ltr"] & {
        transform: translateX(-15px);
      }
    }
  }

  .foot-links {
    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 18px;
      font-family: "tjBold";
      color: #fff;
      margin-bottom: 25px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 17px;
        }

        &.info:not(:last-child) {
          margin-bottom: 13px;
        }
        display: flex;

        &.info {
          width: 49%;
          display: inline-block;
          transition: 0.5s;

          html[dir="ltr"] & {
            @include mob480{
              width: 68%;
              &:nth-child(even){
                width: 30%;
              }
            }
          }

          // @include Sm {
          //   width: 30%;
          // }

          a {
            color: #d2e3f9 !important;

            &:hover {
              color: #fff !important;
            }
          }

          &:hover {
            transform: translateX(-5px);
            html[dir="ltr"] & {
              transform: translateX(5px);
            }
          }
        }

        i {
          font-size: 14px;
          color: $color-white;
          margin-left: 10px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 10px;
          }

          &.fa-phone {
            html[dir="ltr"] & {
              transform: rotateY(180deg);
            }
          }
        }

        a {
          font-size: 16px;
          font-family: "tj";
          backface-visibility: hidden;
          color: #fff;
          display: block;
          width: 100%;
          transition: 0.5s;

          & > p {
            margin: 0;
          }

          &:hover {
            color: $color-white;
          }
        }
      }
    }
  }
}

// End Of Footer

// End

.end {
  border-top: 1px solid #4e76aa;
  margin-top: 35px;

  @include Xs {
    margin-top: 10px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;

    @include mob600 {
      flex-direction: column-reverse;
      padding: 10px 0;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 13px;
      color: #fafafa;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-top: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        margin-right: 5px;

        // @include mob600 {
        //   margin-right: 0;
        // }

        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 10px;
      }
    }

    .social {
      a {
        i {
          font-size: 16px;
          color: #9bc1f1;
          transition: 0.5s;
        }

        &:not(:last-child) {
          margin-left: 22px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 22px;
          }
        }

        &:hover {
          i {
            color: #fff;
          }
        }
      }
    }
  }
}

// End

// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-prim;
  @include Flex;
  z-index: 9999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid $color-prim;
    border-left: 4px solid $color-white;
    animation: spin 1s linear infinite;
  }
}

