html {
  overflow-y: hidden;
  scrollbar-width: thin;
  scrollbar-color: #4a71a3 #b4b4b4;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #B4B4B4;
}

html::-webkit-scrollbar-thumb {
  background-color: #4A71A3;
}

body {
  font-family: 'tj', sans-serif;
  overflow-x: hidden;
}

* {
  font-family: inherit;
}

input, a, button {
  font-family: 'tj', sans-serif;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "tj";
  src: url("../fonts/Tajawal-Regular.ttf") format("ttf"), url("../fonts/Tajawal-Regular.woff") format("woff"), url("../fonts/Tajawal-Regular.woff2") format("woff2"), url("../fonts/Tajawal-Regular.eot") format("eot");
}

@font-face {
  font-family: "tjBold";
  src: url("../fonts/Tajawal-Bold.ttf") format("ttf"), url("../fonts/Tajawal-Bold.woff") format("woff"), url("../fonts/Tajawal-Bold.woff2") format("woff2"), url("../fonts/Tajawal-Bold.eot") format("eot");
}

@font-face {
  font-family: "tjLight";
  src: url("../fonts/Tajawal-Light.ttf") format("ttf"), url("../fonts/Tajawal-Light.woff") format("woff"), url("../fonts/Tajawal-Light.woff2") format("woff2"), url("../fonts/Tajawal-Light.eot") format("eot");
}

@font-face {
  font-family: "tjMd";
  src: url("../fonts/Tajawal-Medium.ttf") format("ttf"), url("../fonts/Tajawal-Medium.woff") format("woff"), url("../fonts/Tajawal-Medium.woff2") format("woff2"), url("../fonts/Tajawal-Medium.eot") format("eot");
}

@font-face {
  font-family: "tjBlk";
  src: url("../fonts/Tajawal-Black.ttf") format("ttf"), url("../fonts/Tajawal-Black.woff") format("woff"), url("../fonts/Tajawal-Black.woff2") format("woff2"), url("../fonts/Tajawal-Black.eot") format("eot");
}

@keyframes flash {
  0% {
    opacity: .6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(64, 106, 159, 0.98);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.navover span {
  display: none;
  color: #FFF;
  font-size: 25px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.navover.open span {
  display: block;
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  z-index: 9999;
  overflow-y: auto;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);
}

html[dir="ltr"] .sideNav {
  transform-origin: left;
}

.sideNav.open {
  transform: scaleX(1);
}

.sideNav.open ul {
  opacity: 1;
}

.sideNav.open ul li {
  transform: translateX(0);
  opacity: 1;
}

.sideNav.origin {
  transform-origin: left;
}

html[dir="ltr"] .sideNav.origin {
  transform-origin: right;
}

.sideNav .options {
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 50px;
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #4b4c4c;
  width: 32px;
  height: 32px;
  background-color: transparent;
}

.sideNav .options .search_pop {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #B4B4B4;
  color: #fff;
  font-size: 14px;
  margin-left: auto;
  margin-right: 20px;
}

html[dir="ltr"] .sideNav .options .search_pop {
  margin-left: 20px;
  margin-right: auto;
}

.sideNav .options .close1 {
  display: flex;
  font-size: 18px;
  color: #777;
  cursor: pointer;
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.sideNav > ul > li {
  position: relative;
  padding: 10px 0;
  transition: 0.5s;
  transform: translateX(10px);
  opacity: 0;
  width: 115px;
}

.sideNav > ul > li:nth-child(1) {
  transition-delay: 0.3s;
}

.sideNav > ul > li:nth-child(2) {
  transition-delay: 0.5s;
}

.sideNav > ul > li:nth-child(3) {
  transition-delay: 0.7s;
}

.sideNav > ul > li:nth-child(4) {
  transition-delay: 0.9s;
}

.sideNav > ul > li:nth-child(5) {
  transition-delay: 1.1s;
}

.sideNav > ul > li:nth-child(6) {
  transition-delay: 1.3s;
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-size: 17px;
  color: #4b4c4c;
  text-align: center;
  transition: 0.5s;
}

.sideNav > ul > li .dropDown {
  display: none;
  margin-top: 10px;
}

.sideNav > ul > li .dropDown li:not(:last-child) {
  margin-bottom: 6px;
}

.sideNav > ul > li .dropDown li a {
  font-size: 15px;
  color: #777;
}

.sideNav > ul > li i {
  position: absolute;
  top: 17px;
  left: 17px;
  font-size: 9px;
  color: #4b4c4c;
  transition: 0.5s;
}

html[dir="ltr"] .sideNav > ul > li i {
  left: unset;
  right: 13px;
}

.sideNav > ul > li i.open {
  transform: rotate(180deg);
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: 0.5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

.navBar {
